import { reactive } from "@vue/reactivity"
/**
 * webData {object} : 包含属性 
 * webName<string> 
 * url<string> 
 * icon<string> 
 * baseUrl<string>
 * description<string>
 */

/**
 * des: 调用该函数根据传入参数的不同，可以实现 
 *      1、添加网址信息到 localStorage
 *      2、依据 url 删除 localStorage 中的相关数据
 *      3、直接返回 localStorage 中的数据 
 * @param {object} webData 添加网址数据
 * @param {string} url 删除网址的url
 */
/*返回的收藏网址的数据 */
let collWebArr = reactive([])

export function collOrDelweb(webData = null,url = null){

  webData ? add() : url ? del() : getStorage()

  // 添加数据
  function add() {
    let storage = localStorage.getItem('collWebs')

    if (storage) {

      // 检查是否存在该网址
      let hasWeb = checkUrl(webData.url)

      if (!hasWeb) {
        collWebArr.push(webData)
        localStorage.setItem("collWebs",JSON.stringify(collWebArr))
      } else {
        alert("已收藏过该网址")
      }

    } else {

      /*第一次添加网址 */
      collWebArr.push(webData)
      localStorage.setItem("collWebs",JSON.stringify(collWebArr))

    }
  }

  // 删除收藏的网址
  function del() {

    collWebArr.forEach((item,index)=>{

      if(item.url === url){
        collWebArr.splice(index,1)
      }  
    })

    localStorage.setItem("collWebs",JSON.stringify(collWebArr))
  }

  // 返回收藏的网址 用于初始化
  function getStorage(){
    let storage = localStorage.getItem("collWebs")

    /* 初始获取可能没有数据，需要判断一下 */
    if (storage) {
      collWebArr.push(...JSON.parse(storage))
    }

  }
  return {
    collWebArr
  }

}


// 判断该 url 是否已经存在
function checkUrl(url){
  let storage = localStorage.getItem('collWebs');
  storage = JSON.parse(storage)
  let has = false;
  has = storage.some(item => item.url === url);
  
  return has
}