<template>
  <nav>
    <ul>
      <li
        v-for="(item,index) in webdata"
        :key="index"
        @click="tabChange(index)"
        :class="{ active: isIndex === index }"
      >
        <i :class="'iconfont ' + item.icon"></i>
        <span>{{ item.tagName }}</span>
      </li>
    </ul>
  </nav>
</template>
<script>
import { ref } from "@vue/reactivity"
import { inject } from "@vue/runtime-core"

export default {
  props: {
    webdata: {
      type: Object
    }
  },
  setup: function () {
    let isIndex = ref(0)

    let changeTagindex = inject("changeTagIndex")
    
    // 点击切换 Tag 
    function tabChange(index) {
      isIndex.value = index
      changeTagindex(index)
    }

    return {
      isIndex,
      tabChange
    }
  },
  mounted() {

  },
  methods: {

  }
}
</script>
<style lang="scss" scoped>
ul {
  width: 100%;
  list-style: none;
  li {
    height: 50px;
    // background-color: rgb(189, 189, 246);
    margin-bottom: 10px;
    padding-left: 20px;
    line-height: 50px;
    border-radius: 10px;
    &:hover {
      font-weight: bolder;
      cursor: pointer;
    }
    .iconfont {
      font-size: 20px;
      margin-right: 20px;
    }
  }
  .active {
    background-color: #ccffff70;
    color: #a8a8fd;
    font-weight: bolder;
    font-size: 22px;
  }
}
</style>