export default function (){
  let getWebData = () => new Promise((resolve, reject) => {
    fetch("./webData.json")
      .then(res => res.json())
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        console.log("获取数据出错", err)
        reject(err)
      })
  })
  return {
    getWebData
  }
}