<template>
  <div class="container" @click.stop="toWeb(webItemData.url)">
    <div class="imgBox">
      <img :src="baseUrl + webItemData.icon" alt />
    </div>
    <div class="infoBox">
      <p>{{ webItemData.webName }}</p>
      <p>{{ webItemData.description }}</p>
    </div>
    <div class="btnBox">
      <i
        v-if="addIconShow"
        class="iconfont icon-add"
        @click.stop="collOrDelweb({ baseUrl, ...webItemData })"
      ></i>
      <i v-else class="iconfont icon-delete" @click.stop="collOrDelweb(null, webItemData.url)"></i>
    </div>
  </div>
</template> 
<script>
import { collOrDelweb } from "@/hooks/userCollWeb.js"
export default {
  data() {
    return {

    }
  },
  props: {
    baseUrl: String,
    webItemData: Object,
    addIconShow: Boolean   // 展示 add 图标 还是 del 图标
  },
  setup() {
    // 点击跳转事件
    function toWeb(url) {
      window.open(url)
    }
    return { toWeb, collOrDelweb }
  }
}
</script>
<style lang="scss" scoped>
.container {
  /* 组件靠内显示  删除 */
  // margin: 10px;
  /* 组件靠内显示 */
  width: 250px;
  height: 70px;
  border-radius: 5px;
  border: 1px solid #ccffffaa; // 后续删除
  background-color: #cadcef59;
  box-sizing: border-box;
  display: flex;
  color: #494949;
  &:hover {
    cursor: pointer;
    background: #99ccff59;
    border-color: #99ccff;
    // 设置图标显示
    & > .btnBox {
      visibility: visible;
    }
  }

  // 网址图标
  & > .imgBox {
    width: 70px;
    height: 70px;
    text-align: center;
    & > img {
      width: 30px;
      height: 30px;
      margin-top: 15px;
      border-radius: 50%;
    }
  }

  // 网址信息栏
  & > .infoBox {
    flex: 1;
    width: 0;
    & > p:nth-child(1) {
      font-size: 14px;
      margin-top: 5px;
      font-weight: bolder;
      display: block;
      width: 100%;
      height: 21px;
      line-height: 21px;

      // 溢出省略
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    & > p:nth-child(2) {
      font-size: 12px;
      display: -webkit-box;
      line-height: 15px;
      height: 30px;
      margin-top: 5px;

      overflow: hidden;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
    }
  }

  // 删除 添加按钮
  & > .btnBox {
    width: 30px;
    text-align: center;
    visibility: hidden; // 默认按钮不可见
    .iconfont {
      font-size: 20px;
      font-weight: bolder;
      display: block;
      margin-top: 5px;
    }

    .icon-add {
      color: #a5a5ff;
    }
    .icon-delete {
      color: #cccccc;
    }
  }
}
</style>