<template>
  <div class="itemscontainer" ref="websContainer">
    <div v-show="collWebArr.length">
      <h3>收藏网址</h3>
      <div class="itemsBox">
        <web-item
          v-for="webitem in collWebArr"
          :key="webitem.url"
          :webItemData="webitem"
          :baseUrl="webitem.baseUrl"
          :addIconShow="false"
        ></web-item>
        <i v-for="i in 20" :key="i"></i>
        <div v-for="(item,index) in test" :key="index">{{ item.name }}</div>
      </div>
    </div>
    <div v-for="(item,index) in webdata" :key="index">
      <h3 :ref="item.class">{{ item.tagName }}</h3>
      <div class="itemsBox">
        <web-item
          v-for="webitem in item.webItems"
          :key="webitem.url"
          :webItemData="webitem"
          :baseUrl="item.baseUrl"
          :addIconShow="true"
        ></web-item>
        <i v-for="i in 10" :key="i"></i>
      </div>
    </div>
  </div>
</template>
<script>

import WebItem from "@/components/WebItem"
import { collOrDelweb } from "@/hooks/userCollWeb.js"
import { reactive } from '@vue/reactivity'

export default {
  inject: ["webdata", "tagIndex", "changeTagIndex"],
  setup() {

    let { collWebArr } = collOrDelweb(null, null)
    let test = reactive([])

    return {
      collWebArr,
      test
    }
  },
  mounted() {

  },
  watch: {
    // 监听到点击了侧边栏，获取到 点击的是哪一栏
    tagIndex(newValue, oldValue) {
      let Y = []
      this.webdata.forEach(item => {
        let Dom = this.$refs[item.class]
        Y.push(Dom.offsetTop)
      });
      window.scroll({
        top: Y[newValue],
        left: 0,
        behavior: "smooth"
      })
    }
  },
  components: {
    WebItem
  }
}
</script>
<style lang="scss" scoped>
.itemscontainer {
  width: 100%;
  // background-color: rosybrown;
  & h3 {
    margin-bottom: 10px;
  }
  .itemsBox {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    & > .container:last-child {
      margin-right: auto;
    }
    & > .container {
      margin: 0px 20px 20px 20px;
    }
    & > i {
      width: 250px;
      margin: 0px 20px 0px 20px;
    }
  }
}
</style>