<template>
  <div class="searchContainer">
    <div class="searchEng">
      <li
        v-for="({ name, url },index) in searchEngine"
        :key="url"
        :class="{ active: isIndex === index }"
        @click="engineChange(index)"
      >{{ name }}</li>
    </div>
    <div class="inputBox">
      <input type="text" v-model="kw" @keydown.enter="search" placeholder="请输入关键词" />
      <div class="btnBox" @click="search">
        <i class="iconfont icon-search"></i>
      </div>
    </div>
  </div>
</template> 
<script>
import { ref } from "@vue/reactivity"
import { watch } from "@vue/runtime-core"
export default {
  data() {
    return {
    }
  },
  setup: function () {
    let searchEngine = [
      {
        name: "必应",
        url: "https://cn.bing.com/search?q="
      },
      {
        name: "百度",
        url: "https://www.baidu.com/s?wd="
      }
    ]
    let kw = ref("")
    let isIndex = ref(0)
    let engine = ref("https://cn.bing.com/search?q=")


    // 改变搜索引擎
    function engineChange(index) {
      isIndex.value = index;
      engine.value = searchEngine[index].url
    }


    // 搜索
    function search() {
      window.open(`${engine.value}${kw.value}`)
    }

    return {
      kw,
      isIndex,
      searchEngine,
      engineChange,
      search
    }
  }
}
</script>
<style lang="scss" scoped>
.searchContainer {
  width: 800px;
  height: 150px;
  // background-color: rgba($color: #000000, $alpha: 0.1);
  .searchEng {
    width: 200px;
    height: 50px;
    margin: auto;
    & > li {
      list-style: none;
      display: inline-block;
      width: 100px;
      height: 100%;
      text-align: center;
      line-height: 50px;
      cursor: pointer;
    }
    .active {
      font-weight: bolder;
      box-sizing: border-box;
      border: 2px solid #99ccff;
      border-bottom: none;
      background-color: #99ccff8a;
      border-radius: 5px 5px 0px 0px;
    }
  }
  .inputBox {
    width: 100%;
    height: 60px;
    border-radius: 30px;
    background-color: #99ccff8a;
    display: flex;
    align-items: center;
    &:hover {
      box-shadow: 0px 8px 8px #c1d6dd8a;
    }
    & > input {
      width: 650px;
      margin-left: 20px;
      height: 30px;
      border: none;
      background-color: #99ccff00;
      outline: none;
    }
  }
  .btnBox {
    width: 100px;
    height: 50px;
    background-color: #d5d5f8;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 25px;
    cursor: pointer;
    i {
      font-size: 30px;
    }
    &:hover {
      background-color: #ccccff;
    }
  }
}
</style>