<template>
  <div style="height: 100%" class="appContainer">
    <transition name="sideBar">
      <div class="sideBar" v-show="showSideBar">
        <side-bar></side-bar>
      </div>
    </transition>
    <main>
      <div class="mainBox">
        <i class="iconfont icon-more" @click="sideBarIsShow"></i>
        <search class="searchBox"></search>
        <!-- <div class="yedu">
          <p>人民日报夜读</p>
          <ol>
            <li v-for="item in yedu" :key="item.mblog.id" v-html="item.mblog.text"></li>
          </ol>
        </div>-->
        <web-container class="webs"></web-container>
      </div>
    </main>
    <div v-if="false" class="rightBox">
      <!-- <i class="iconfont icon-toTop"></i> -->
    </div>
  </div>
</template>

<script>
import Search from "@/components/Search"
import WebContainer from "@/views/WebContainer"
import SideBar from "@/views/SideBar"

import userGetWebdata from '@/hooks/userGetWebData.js'
// import { getYedu } from "@/hooks/userGetYedu.js"

import { computed, ref } from '@vue/reactivity'
import { provide } from "vue"
export default {
  setup: function () {
    // 获取网址数据的异步函数，放在 mounted 钩子中调用
    let { getWebData } = userGetWebdata()
    let tagindex = ref(0)
    provide("tagIndex", tagindex)
    provide("changeTagIndex", function (index) {
      tagindex.value = index
    })

    // 展示 sideBar
    let showSideBar = ref(false)
    function sideBarIsShow() {
      showSideBar.value = !showSideBar.value
    }
    return {
      getWebData,
      showSideBar,
      sideBarIsShow
    }
  },
  data() {
    return {
      webdata: {},
      yedu: []
    }
  },
  async mounted() {
    let webdata;
    let yedu;
    try {
      webdata = await this.getWebData()
      // yedu = await this.getYedu()
    } catch (e) {
      console.log("获取网页数据失败", e)
      alert("获取网址数据失败，请刷新页面重试")
    }
    this.webdata = webdata
    /* 夜读数据 */
    // this.yedu = yedu.data.cards[0].card_group
  },
  provide() {
    return {
      webdata: computed(() => this.webdata)
    }
  },
  components: {
    SideBar,
    Search,
    WebContainer
  }
}
</script>

